"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Dialog, DialogContent } from "@/components/ui/dialog"

const galleryImages = [
  { src: "/computer-class-students-learning.jpg", alt: "Students in computer class" },
  { src: "/certificate-distribution-ceremony.jpg", alt: "Certificate distribution" },
  { src: "/annual-function-celebration-school.jpg", alt: "Annual function" },
  { src: "/students-group-photo-institution.jpg", alt: "Group photo" },
  { src: "/computer-lab-equipment-modern.jpg", alt: "Computer lab" },
  { src: "/diwali-celebration-office-decoration.jpg", alt: "Diwali celebration" },
  { src: "/independence-day-celebration-flag.jpg", alt: "Independence Day" },
  { src: "/teachers-day-celebration-students.jpg", alt: "Teachers Day" },
]

export function Gallery() {
  const [selectedImage, setSelectedImage] = useState<string | null>(null)

  return (
    <section id="gallery" className="py-10 md:py-16">
      <div className="container mx-auto px-4">
        <div className="mb-8 md:mb-12 text-center">
          <h2 className="mb-3 md:mb-4 text-2xl md:text-3xl lg:text-4xl font-bold text-foreground">
            Our <span className="text-primary">Gallery</span>
          </h2>
          <p className="mx-auto max-w-2xl text-sm md:text-base text-muted-foreground px-2">
            Glimpses of our events, celebrations, and the vibrant learning environment at ACC.
          </p>
        </div>

        <div className="grid gap-3 md:gap-4 grid-cols-2 sm:grid-cols-3 lg:grid-cols-4">
          {galleryImages.map((img, i) => (
            <Card key={i} className="group cursor-pointer overflow-hidden" onClick={() => setSelectedImage(img.src)}>
              <div className="relative aspect-square sm:aspect-video overflow-hidden">
                <img
                  src={img.src || "/placeholder.svg"}
                  alt={img.alt}
                  className="h-full w-full object-cover transition-transform duration-300 group-hover:scale-110"
                />
                <div className="absolute inset-0 bg-primary/0 transition-colors group-hover:bg-primary/20" />
              </div>
            </Card>
          ))}
        </div>
      </div>

      <Dialog open={!!selectedImage} onOpenChange={() => setSelectedImage(null)}>
        <DialogContent className="max-w-[95vw] md:max-w-4xl p-2 md:p-4">
          {selectedImage && (
            <img src={selectedImage || "/placeholder.svg"} alt="Gallery image" className="w-full rounded-lg" />
          )}
        </DialogContent>
      </Dialog>
    </section>
  )
}
